/* eslint-disable no-console */
<template>
  <div name="createSoftWare" class="softWare">
    <h1 name="title">{{ id ? "" : "" }}:</h1>
    <el-form
      ref="model"
      :rules="rules"
      :model="model"
      label-width="120px"
      @submit.native.prevent="save('model')"
      class="sendItem"
    >
      <el-form-item label="" prop="versionNumber">
        <el-input
          id="versionNum"
          maxlength="16"
          v-model="model.versionNumber"
        ></el-input>
      </el-form-item>
      <el-form-item label="" prop="versionType">
        <el-select
          value-key="model.versionType"
          v-model="model.versionType"
          placeholder=""
        >
          <el-option
            v-for="item in versionType"
            :key="item"
            :label="item"
            :value="item"
          ></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="" prop="relatedProject">
        <el-select
          value-key="model.relatedProject._id"
          v-model="model.relatedProject"
          multiple
          placeholder=""
        >
          <el-option
            v-for="item in projectList"
            :key="item._id"
            :label="item.projectName"
            :value="item._id"
          ></el-option>
        </el-select>
        <el-tooltip
          class="item"
          effect="dark"
          content=""
          placement="top-start"
        >
          <span class="help el-icon-question"></span>
        </el-tooltip>
      </el-form-item>
      <el-form-item label="" prop="versionFeatures">
        <vue-editor v-model="model.versionFeatures"></vue-editor>
      </el-form-item>
      <el-form-item label="">
        <el-row v-if="model.fileName">
          <el-link
            class="fileLink"
            v-bind:href="model.fileDir"
            target="_blank"
            >{{ model.fileName }}</el-link
          >
          <el-button
            size="mini"
            @click="removeFile"
            type="danger"
            icon="el-icon-delete"
            circle
          ></el-button>
        </el-row>
        <div v-if="!model.fileName">
          <el-upload
            class="file-upload"
            :action="$http.defaults.baseURL + '/upload'"
            :on-success="afterSuccess"
            :before-upload="handleProgress"
            :data="fileData"
          >
            <el-button size="small" type="primary"></el-button>
            <div slot="tip" class="el-upload__tip">
              100M
            </div>
          </el-upload>
        </div>
      </el-form-item>
      <el-form-item style="margin-top: 1rem;">
        <el-button type="primary" class="save" native-type="subumit"
          ></el-button
        >
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
import { VueEditor } from "vue2-editor";
import { restgetAll, restgetOne } from "../../Api/api";
import { restUpdata, restPostData, deleteFile } from "../../Api/api";
export default {
  name: "SoftWareCreate",
  props: {
    id: {},
  },
  data() {
    return {
      model: { fileName: "" },
      versionType: ["WebMc", "Mc", "An", "Ac"],
      projectList: [],
      fileData: { fileName: "" },
      rules: {
        versionNumber: [
          { required: true, message: "", trigger: "blur" },
          {
            min: 3,
            max: 15,
            message: " 3  15 ",
            trigger: "blur",
          },
        ],
        relatedProject: [
          {
            type: "array",
            required: true,
            message: "",
            trigger: "blur",
          },
        ],
        versionType: [
          {
            type: "string",
            required: true,
            message: "",
            trigger: "blur",
          },
        ],
        versionFeatures: [
          { required: true, message: "", trigger: "blur" },
        ],
      },
    };
  },
  components: {
    VueEditor,
  },
  methods: {
    async fetchProject() {
      const res = await restgetAll("project");
      this.projectList = res.data;
    },

    async fetchEdit() {
      const data = await restgetOne("softWare", this.id);
      this.model = data.data;
    },

    async save(formName) {
      await this.$refs[formName].validate(); //
      let res;
      if (this.id) {
        this.model.upDateTime = new Date().toLocaleString(); //
        res = await restUpdata("softWare", this.id, this.model);
      } else {
        res = await restPostData("softWare", this.model);
      }
      this.$router.push("/softWare/list");
      this.$notify({
        title: "",
        type: "success",
        message: "",
      });
    },

    afterSuccess(file) {
      //model
      this.model.fileDir = file.url;
      this.model.fileName = file.filename;
    },

    async handleProgress(file) {
      if (file.size > 100000000) {
        this.$confirm("100M");
        return false;
      }
      if (this.model.versionNumber == "") {
        this.$confirm("");
        return false;
      }
      if (this.model.relatedProject == "") {
        this.$confirm("");
        return false;
      }
      let reId = this.model.relatedProject[0];
      if (this.id) {
        reId = this.model.relatedProject[0]._id; //
      }
      console.log(`reID`, reId);
      const getProjectName = await restgetOne("project", reId); //
      return (this.fileData.fileName = `${this.model.versionType}_${this.model.versionNumber}_${getProjectName.data.projectName}_${file.name}`);
    },

    removeFile() {
      return this.$confirm(` ${this.model.fileName}`)
        .then(async () => {
          const res = await deleteFile(this.model.fileName);
          this.model.fileName = "";
        })
        .catch((err) => {
          console.log(``, err);
        });
    },
  },
  created() {
    this.id && this.fetchEdit();
    this.fetchProject();
  },
};
</script>

<style>
.fileLink {
  margin-right: 10px;
}
.help {
  margin: 5px;
  color: #cccccc;
}
</style>
